.footer-page {
	padding-top: 80px;
	padding-bottom: 80px;
}

.footer-page__wrapper {
	display: flex;
	align-items: stretch;
	justify-content: space-between;
}
.footer-page__contacts {
	max-width: 345px;
}
.footer-page__title {
	font-family: "Montserrat";
	font-weight: 500;
	font-size: 24px;
	letter-spacing: 0.06em;
	margin-bottom: 25px;
}

.footer-page__name-company,
.footer-page__link-point,
.footer-page__link-tel {
	font-weight: 300;
	font-size: 15px;
}
.footer-page__name-company {
	margin-bottom: 20px;
}
.footer-page__link-tel {
	display: block;
	font-weight: 600;
	margin-bottom: 20px;
	transition: color 0.3s ease;

	&:hover {
		color: var(--color-primary);
		text-decoration: underline;
	}
}

.footer-page__text-point {
	span {
		display: block;
	}
}

.footer-page__date-block {
	display: flex;
	flex-direction: column;
}

.footer-page__socials-list {
	display: flex;
	align-items: center;
	column-gap: 20px;
	flex: 1 1 auto;
}

.icon-socials {
	height: 50px;
	width: 50px;
}

svg.icon-socials.icon-socials--ok {
	width: 45px;
}
.socials-link svg.icon-socials {
	transition: fill 0.3s ease;
}
.socials-link:hover svg.icon-socials {
	fill: var(--color-primary);
}

.date-block {
	font-family: Montserrat;
	font-style: normal;
	font-weight: 500;
	font-size: 16px;
	line-height: 22px;
	letter-spacing: 0.06em;
}

.scroll-top {
	position: fixed;
	bottom: 80px;
	right: 80px;
	z-index: 99;
}

.scroll-top .btn-scroll-top {
	display: none;
	position: relative;
	overflow: hidden;
	height: 80px;
	width: 80px;
	border: 5px solid var(--color-primary);
	outline: none;
	cursor: pointer;
	font-size: 16px;
	background-color: var(--color-secondary);
	transition: all 0.3s ease;
	border-radius: 50%;
	box-shadow: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);

	&:hover {
		background-color: var(--color-primary);

		.icon-arrow-top {
			fill: var(--color-secondary);
		}
	}
}

.icon-arrow-top {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 40px;
	height: 40px;
	transform: translate(-50%, -50%);
	fill: var(--color-primary);
	transition: fill 0.3s ease;
}

@media (max-width: 768px) {
	.footer-page {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	.scroll-top {
		bottom: 25px;
		right: 25px;
	}
	.scroll-top .btn-scroll-top {
		border-width: 3px;
		height: 50px;
		width: 50px;
	}
	.icon-arrow-top {
		width: 20px;
		height: 20px;
	}
}

@media (max-width: 568px) {
	.footer-page__contacts {
		max-width: 100%;
	}
	.footer-page__wrapper {
		flex-direction: column;
		row-gap: 20px;
	}
}
