.section-page.study {
	padding-bottom: 100px;
}

.study__content {
	display: flex;
	flex-direction: column;
	row-gap: 35px;
}

.study__row {
	display: flex;
	align-items: center;
	column-gap: 45px;
}

.study__col-img {
	flex: 0 0 auto;
	width: 200px;
	height: 200px;
	border-radius: 50%;
	overflow: hidden;
}

.study__text {
	font-weight: 600;
	font-size: 20px;
	line-height: 1.275;
	letter-spacing: 0.01em;
	color: var(--color-third);
}

.study__subtext {
	font-weight: 500;
	font-size: 16px;
	line-height: 1.25;
	letter-spacing: 0.01em;
	margin-top: 80px;
}

.study__subtext-link {
	color: var(--color-primary);

	&:hover {
		text-decoration: underline;
	}
}

@media (max-width: 768px) {
	.study__col-img {
		width: 150px;
		height: 150px;
	}
	.study__subtext {
		margin-top: 40px;
	}
	.section-page.study {
		padding-bottom: 40px;
	}
}

@media (max-width: 576px) {
	.study__col-img {
		width: 75px;
		height: 75px;
	}
	.study__row {
		column-gap: 15px;
	}
	.study__text {
		font-size: 16px;
	}
}
@media (max-width: 450px) {
	.study__col-img {
		display: none;
	}
	.study__content {
		row-gap: 15px;
	}
	.section-page {
		padding-top: 15px;
		padding-bottom: 15px;
	}
}
