.participate {
	background-color: var(--color-primary);
}

.participate .section-title {
	color: var(--color-secondary);
}
.participate__list {
	display: flex;
	flex-direction: column;
	row-gap: 40px;
}
.participate__list-item {
	display: flex;
	align-items: flex-start;
	background-color: var(--color-secondary);
	padding: 12px 40px 24px 30px;
	border-radius: 10px;
}

.participate__num {
	flex: 0 0 auto;
	display: flex;
	align-items: center;
	justify-content: center;
	width: 35px;
	height: 35px;
	border-radius: 50%;
	background-color: var(--color-primary);
	font-weight: 600;
	font-size: 20px;
	color: var(--color-secondary);
	margin-right: 20px;
}

.participate__text {
	font-weight: 600;
	font-size: 24px;
	line-height: 1.4;
	letter-spacing: 0.01em;
	color: var(--color-primary-dark);
}

.participate__link-wrapper {
	display: flex;
	align-items: center;
	justify-content: center;
	margin-top: 70px;
}

.participate__link {
	font-weight: 500;
	font-size: 32px;
	color: var(--color-secondary);
	border: 2px solid var(--color-secondary);
	border-radius: 15px;
	padding: 5px 18px;
	letter-spacing: 0.005em;
	background-color: transparent;
	transition: all 0.3s ease;
}

.participate__link:hover {
	color: var(--color-primary);
	background-color: var(--color-secondary);
}

@media (max-width: 768px) {
	.participate__list-item {
		padding: 10px 30px 20px 15px;
	}
	.participate__list {
		row-gap: 20px;
	}
}

@media (max-width: 576px) {
	.participate__num {
		width: 30px;
		height: 30px;
		font-size: 14px;
		margin-right: 10px;
	}
	.participate__text {
		font-size: 16px;
		line-height: 1.2;
	}
	.participate__link-wrapper {
		margin-top: 35px;
	}
}

@media (max-width: 450px) {
	.participate__list-item {
		padding: 10px 10px 10px 10px;
	}
	.participate__link {
		font-size: 28px;
	}
}
