.passport__content {
	color: var(--color-third);
	padding-left: 65px;
}

.passport__text {
	max-width: 890px;
	font-weight: 500;
	font-size: 24px;
	line-height: 1.3;
	letter-spacing: 0.01em;

	span {
		font-weight: 600;
	}
}

.passport__row-img {
	display: flex;
	align-items: center;
	column-gap: 60px;
	padding-top: 50px;
	padding-bottom: 50px;
}

.passport__subtext {
	font-weight: 600;
	font-size: 20px;
	line-height: 1.2;
	letter-spacing: 0.01em;
	text-align: center;
	color: var(--color-third);
}

@media (max-width: 576px) {
	.passport__content {
		padding-left: 0;
	}
}

@media (max-width: 450px) {
	.passport__text {
		font-size: 16px;
	}
	.passport__row-img {
		row-gap: 30px;
		flex-direction: column;
	}
}
